/* main2/SecondSection.module.css */
@keyframes netflixIntro {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.secondSection {
    width: 100%;
    height: 100vh;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.secondSectionText {
    opacity: 0;
    font-size: 60px;
    color: white;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
    animation: netflixIntro 2s ease-in-out forwards;
}